import { sys } from "cc";

export default class HTTP {
    public Url = 'http://127.0.0.1:3000';
    public url = this.Url;
    public master_url = this.Url;
    public token = null;

    public static instance = null;

    static getInstance() {
        if (this.instance == null) {
            this.instance = new HTTP();
            return this.instance;
        }
        else {
            return this.instance;
        }
    }
}